.background {
    position: fixed;
    /* always stays in place */
    top: 0;
    left: 0;
    width: 100vw;
    height: 110vh;
    overflow: hidden;
    z-index: -1;
    scrollbar-width: hidden;
    /* behind all content */
}

.spline {
    width: 100%;
    height: 100%;
}


.main {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

/* Foreground content */
.content {
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.content h1 {
    color: white;
    font-size: 2.5rem;
    /* same as Tailwind's text-4xl */
    font-weight: bold;
}