@font-face {
    font-family: 'PatriciaSans';
    src: url('./patricia_sans/PatriciaSans.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

h1 {
    text-align: center;
    font-family: 'PatriciaSans', sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
}

.sodha {
    position: relative;
    z-index: 1;
    color: white;
    padding: 2rem;
    font-size: 40px;
    margin-top: -40px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.cards-container {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.glass-card {
    backdrop-filter: blur(10px);
    background: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    padding: 1.5rem;
    width: 650px;
    text-align: center;
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.glass-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 12px 40px 0 rgba(0, 0, 0, 0.5);
}

.glass-card h2 {
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
}

.glass-card p {
    font-size: 1rem;
}

.card-image {
    width: 580px;
    height: auto;
    border-radius: 10px;
}

.manipulation-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
    gap: 0.5rem;
    align-items: center;
    justify-items: start;
}

.manipulation-container {
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    padding: 0.5rem;
}

button {
    background: linear-gradient(135deg, #4f8ef7, #6fc1ff);
    /* modern gradient */
    color: #fff;
    /* white text */
    font-size: 1rem;
    /* readable text */
    font-weight: 600;
    /* bold text */
    padding: 0.75rem 1.5rem;
    /* comfortable padding */
    border: none;
    /* no default border */
    border-radius: 0.75rem;
    /* rounded corners */
    cursor: pointer;
    /* pointer on hover */
    transition: all 0.3s ease;
    /* smooth animation */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    /* subtle shadow */
    text-transform: uppercase;
    /* uppercase text for style */
    letter-spacing: 1px;
    /* slight spacing */
}

/* Hover effect */
button:hover {
    transform: translateY(-3px) scale(1.05);
    background: linear-gradient(135deg, #6fc1ff, #4f8ef7);
    /* gradient flip */
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

/* Active/Click effect */
button:active {
    transform: scale(0.97);
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

/* Optional focus style for accessibility */
button:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(111, 193, 255, 0.5);
}

/* Optional: small and large variants */
button.small {
    font-size: 0.85rem;
    padding: 0.5rem 1rem;
}

button.large {
    font-size: 1.2rem;
    padding: 1rem 2rem;
}